#header {
  width: 100%;
  position: fixed;
  background-color: var(--c4);
  z-index: 1000;
  box-shadow: 0 1px 10px -5px black;
  display: grid;
}

#header > div#icon {
  background: url(data:image/png;base64,);
  height: 80%;
  width: 100px;
  background-size: contain;
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  top: 10px;
}

#header > div {
  margin: auto;
}

#header > div > button {
  background-color: transparent;
  border: 1px solid transparent;
  padding: 30px 20px;
  cursor: pointer;
  outline: none;
}

#header > div > button.active,
#header > div > button:hover {
  background-color: var(--c2);
  color: var(--c4);
}

#header + * {
  padding-top: 80px;
}

#header + div {
  height: calc(100% - 80px);
}

#recipes-explorer-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px 20px;
  gap: 40px 20px;
  padding: 60px 50px;
}

#recipes-explorer-container > div {
  /* width: calc(25vw - 35px); */
  padding: 10px;
  box-shadow: 0 1px 7px -4px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  background-color: var(--c1);
}

#recipes-explorer-container > div:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-color: var(--c4);
  color: var(--c1);
}

#recipes-explorer-container > div > img {
  max-height: 200px;
  width: 100%;
}

#recipes-explorer-container > div > span {
  text-transform: capitalize;
  display: block;
  text-align: center;
  padding: 10px 10px 20px;
  font-weight: bold;
  min-height: 40px;
}

#recipes-explorer-container > div > div.recipe-item-footer {
  padding: 20px 0;
}

#recipes-explorer-container > div > div.recipe-item-footer > div {
  padding: 0 10px;
  display: inline-block;
}

#recipes-explorer-container > div > div.recipe-item-footer > div > * {
  font-size: 0.8em;
  padding: 0 2px;
}

#recipe-container {
  padding: 40px;
}

#recipe-container > div:nth-child(1) {
  display: grid;
  /* grid-template-columns: 1fr 2fr; */
}

#recipe-container > div:nth-child(1) > div {
  margin: auto;
  color: var(--c1);
  text-align: center;
}

#recipe-container > div:nth-child(1) > div > div > a,
#recipe-container > div:nth-child(1) > div > div > a:visited {
  color: var(--c4);
}

#recipe-container > div:nth-child(1) > div:nth-child(1) > h1 {
  text-align: center;
}

#recipe-container > div:nth-child(1) > div:nth-child(2) > div {
  display: inline-block;
  margin: 10px;
}

#recipe-container > div:nth-child(1) > div:nth-child(2) > div > i {
  padding-right: 2px;
}

#recipe-container > div:nth-child(2) > .step {
  margin: 20px 0 30px;
  border-radius: 10px;
  padding: 0 20px;
  box-shadow: 0 1px 7px -5px;
  background-color: var(--c1);
}

#recipe-container > div:nth-child(2) > .step > div:nth-child(1) {
  background-color: var(--c3);
  margin: 0 -20px;
  padding: 0 20px;
  border-radius: 10px;
  box-shadow: 0 1px 7px -5px;
}

#recipe-container > div:nth-child(2) > .step > div > span {
  display: block;
  padding: 20px 0;
  color: var(--c2);
}

#recipe-container > div:nth-child(2) > .step > div > span:nth-child(1) {
  font-weight: bold;
  font-size: 1.5em;
  color: black;
}

#recipe-container > div:nth-child(2) > .step > div > div.extra > span:nth-child(1) {
  display: inline-block;
  padding: 0;
  margin: 50px 0 0 0;
  width: 40px;
  -webkit-transform: rotate(-90deg) translateY(50%);
          transform: rotate(-90deg) translateY(50%);
}

#recipe-container
  > div:nth-child(2)
  > .step
  > div
  > div.extra.equipment:nth-child(3)
  > span:nth-child(1) {
  margin: 0;
}

#recipe-container
  > div:nth-child(2)
  > .step
  > div
  > div.extra.equipment:nth-child(1)
  > span:nth-child(1) {
  margin: 70px 30px 30px -35px;
}

#recipe-container > div:nth-child(2) > .step > div > .extra {
  display: flex;
  padding: 30px 0;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > span {
  font-weight: bold;
  margin: 0 0 20px;
  display: block;
  text-transform: uppercase;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div {
  display: inline-block;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient,
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment {
  /* border: 1px solid red; */
  position: relative;
  display: inline-block;
  padding: 0 20px;
  text-align: center;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > *,
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > * {
  display: block;
  margin: auto;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > span:nth-child(1),
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > span:nth-child(1) {
  font-weight: bold;
  text-transform: capitalize;
  text-align: center;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > span:nth-child(2),
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > span:nth-child(2) {
  color: gray;
  font-size: 0.8em;
  text-transform: capitalize;
  text-align: center;
}

#recipe-container
  > div:nth-child(2)
  > .step
  > div
  > .extra
  > div
  > .ingredient
  > span:nth-child(n + 3),
#recipe-container
  > div:nth-child(2)
  > .step
  > div
  > .extra
  > div
  > .equipment
  > span:nth-child(n + 3) {
  font-size: 0.8em;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > span:nth-child(4),
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > span:nth-child(4) {
  color: grey;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > span:nth-child(5),
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > span:nth-child(5) {
  font-weight: bold;
}

#recipe-container > div:nth-child(2) > .step > div > .extra > div > .ingredient > img,
#recipe-container > div:nth-child(2) > .step > div > .extra > div > .equipment > img {
  height: 60px;
  margin: 10px auto;
}

#login-container {
  height: 100%;
  display: grid;
}

#login-container > div {
  margin: auto;
  width: 30vw;
}

#login-container > div > input {
  border: 2px solid transparent;
  display: block;
  margin: 10px 0;
  padding: 10px;
  outline: none;
  width: calc(100% - 25px);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  text-align: center;
}

#login-container > div > input:focus {
  border: 2px solid var(--c4);
}

#login-container > div > div#logo {
  background-image: url(data:image/png;base64,);
  height: 20vh;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

#login-container > div > div:not(#logo) {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}

#login-container > div > div > button {
  background-color: var(--c4);
  color: var(--c2);
  outline: none;
  padding: 10px 20px;
  border: 1px solid transparent;
  cursor: pointer;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  font-weight: bold;
}

#login-container > div > div > button:hover {
  background-color: var(--c2);
  color: var(--c4);
  border: 1px solid var(--c4);
}

#search-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}

#search-container > div {
  height: calc(100% - 20px);
  padding: 10px;
  position: relative;
}

#search-container > div > div {
  margin: auto;
  text-align: center;
}

#search-container > div:nth-child(1) > div:nth-child(2) {
  overflow-y: auto;
  max-height: calc(70vh + 40px);
  margin-bottom: -20px;
}

#search-container > div > div > button {
  background-color: transparent;
  padding: 10px;
  border: 1px solid var(--c4);
  margin: 10px;
  cursor: pointer;
  color: var(--c4);
  outline: none;
}

#search-container > div > div > button.active,
#search-container > div > div > button:hover {
  background-color: var(--c4);
  color: var(--c1);
}

#search-container > div > #name-search-container {
  height: calc(100% - 80px);
  display: grid;
}

#search-container > div > #name-search-container > div {
  margin: auto;
}

#search-container > div > #name-search-container > div > input {
  margin: auto;
  padding: 30px 30px 20px;
  font-size: 2em;
  width: 60%;
  text-align: center;
  display: block;
  outline: none;
  border: 2px solid transparent;
}

#search-container > div > #name-search-container > div > input:focus {
  border: 2px solid var(--c4);
}

#search-container > div > #name-search-container > div > button {
  background-color: transparent;
  padding: 10px;
  border: 1px solid var(--c4);
  margin: 10px;
  cursor: pointer;
  color: var(--c4);
  width: calc(60% + 60px + 4px);
  outline: none;
}

#search-container > div > #name-search-container > div > button:hover {
  background-color: var(--c4);
  color: var(--c1);
}

#ingredients-search-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  gap: 20px;
  padding: 20px;
}

#ingredients-search-container > span {
  color: var(--c1);
  display: block;
  text-align: center;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
}

#ingredients-search-container > .ingredient-item {
  background-color: var(--c1);
  box-shadow: 0 1px 10px -5px black;
  border: 2px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  padding: 5px;
  min-height: 80px;
}

#ingredients-search-container > .ingredient-item.active {
  background-color: var(--c4);
  display: grid;
}

#ingredients-search-container > .ingredient-item:hover {
  border: 2px solid var(--c4);
}

#ingredients-search-container > .ingredient-item > * {
  display: block;
  text-align: center;
}

#ingredients-search-container > .ingredient-item > img {
  max-height: 50px;
  margin: auto;
}

#ingredients-search-container > .ingredient-item > span {
  text-transform: capitalize;
}

#ingredients-search-container > .ingredient-item.active > img {
  display: none;
}

#ingredients-search-container > .ingredient-item.active > span {
  margin: auto;
  display: flex;
}

.badge {
  background-color: var(--c1);
  border-radius: 50px;
  padding: 4px 10px 2px;
  margin: 5px 4px;
  display: inline-block;
  font-size: 0.8em;
  text-transform: capitalize;
}

#search-container > div > #result-container > div > span#info {
  color: var(--c3);
  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  font-weight: bold;
}

#search-container > div > #result-container > button {
  border-radius: 50%;
  padding: 20px 20px 17px;

  position: fixed;
  bottom: 0;
  right: 00;
  box-shadow: 0 1px 10px -5px black;
  background-color: var(--c4);
  color: var(--c2);
}

#search-container > div > #result-container > button:hover {
  background-color: var(--c2);
  color: var(--c4);
}

#search-container > div > #result-container > #recipes-results-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  gap: 10px;
  margin-top: 25px;
}

#search-container > div > #result-container > #recipes-results-container > .recipe-result-item {
  background-color: var(--c1);
  padding: 10px;
  border-radius: 10px;
  position: relative;
  padding-bottom: 50px;
  border: 2px solid transparent;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

#search-container
  > div
  > #result-container
  > #recipes-results-container
  > .recipe-result-item:hover {
  border: 2px solid var(--c4);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

#search-container
  > div
  > #result-container
  > #recipes-results-container
  > .recipe-result-item
  > img {
  width: 100%;
}

#search-container
  > div
  > #result-container
  > #recipes-results-container
  > .recipe-result-item
  > span {
  display: block;
}

#search-container
  > div
  > #result-container
  > #recipes-results-container
  > .recipe-result-item
  > span:nth-child(3) {
  position: absolute;
  bottom: 0;
  /* background-color: var(--c4); */
  right: 0;
  padding: 10px;
  border-radius: 50%;
  font-size: 0.8em;
  color: var(--c2);
  width: calc(100% - 20px);
}

